<template>
    <div class="Header">
    <n-config-provider >
        <n-grid cols="2">
            <n-grid-item>
                <div class="box">
                        <n-grid cols="9" class="list">
                        <n-grid-item class="box">
                            <a href="#" class="logo">
                                <img src="../../assets/logo.png" alt="" style="">
                            </a>
                        </n-grid-item>
                        <n-grid-item>
                            <router-link :to="{path:'/home'}" tag="a" class="link">首页</router-link>
                        </n-grid-item>
                        <n-grid-item>
                            <router-link :to="{path:'/official'}" tag="a" class="link">官方</router-link>
                        </n-grid-item>
                        <n-grid-item>
                            <router-link :to="{path:'/community'}" tag="a" class="link">社区</router-link>
                        </n-grid-item>
                        <n-grid-item>
                            <router-link :to="{path:'/discuss'}" tag="a" class="link">讨论</router-link>
                        </n-grid-item>
                        <n-grid-item>
                            <router-link :to="{path:'/personal'}" tag="a" class="link">个人</router-link>
                        </n-grid-item>
                    </n-grid>
                </div>
            </n-grid-item>
            <n-grid-item>
                <n-form
                    ref="formRef"
                    :model="formValue"
                    :show-label="false"
                    :show-feedback="false"
                    class="box"
                    inline
                    :rules="rules">
                    <n-form-item>
                        <n-input-group>
                            <n-input v-model:value="value" 
                                autosize
                                type="text" 
                                color="#252525"
                                placeholder="请输入内容"
                                class="search"/>
                            <n-button @click="handleClick" color="#252525">搜索</n-button>
                        </n-input-group>
                    </n-form-item>
                    <n-form-item>
                        <n-space>
                            <n-button-group class="box">
                                <n-button secondary strong @click="notify('success','个人信息')">
                                    <template #icon>
                                        <n-icon>
                                            <Person/>
                                        </n-icon>
                                    </template>
                                </n-button>
                            </n-button-group>
                            <n-button-group class="box">
                                <n-button color="#252525" @click="handleLogin">
                                    登录 | 注册
                                </n-button>
                            </n-button-group>
                            <n-button-group class="box">
                                <n-button secondary strong  @click="notify('success','设置')">
                                    <template #icon>
                                        <n-icon>
                                            <Settings/>
                                        </n-icon>
                                    </template>
                                </n-button>
                            </n-button-group>
                        </n-space>
                    </n-form-item>
                </n-form>
            </n-grid-item>
        </n-grid>
    </n-config-provider>
    </div>
</template>
                                                                                                                                                                                                                                                                                                                    
<script setup>
import { reactive,ref,toRefs } from 'vue'
import { useRouter } from "vue-router" 
import { Person,Settings } from "@vicons/ionicons5"

const router = useRouter()
const state = reactive({
    formValue: {},
    value: ref(''),
    rules: ref({

    })
})

const handleLogin = () => {
    router.push({ path: '/login' })
}

const handleClick = () => {
    window.$message.success('搜索')
}
const notify = (type,msg) => {
    window.$notification[type]({
        content: '通知',
        meta: msg,
        duration: 2500,
        keepAliveOnHover: true
    })
}
 
const { formValue,value,rules } = {...toRefs(state)}
</script>

<style lang="less" scoped>
.Header {
    height: 50px;
    background-color: var(--theme_white);
    .list {
        margin-left: 20px;
    }
    .search {
        min-width: 320px;
    }
}
.logo {
    img {
        width: 40px;
        height: 40px; 
        margin: 10px 0;
    }
}

.link,.box,.logo{
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--theme-font_black);
  text-decoration: none;
  transition: height 1s;    
}
.link:hover{
    height: 50px;
    border-bottom: 2px solid #252525;
    background-color: var(--theme_gray);
}
.box:last-child{
    justify-content: space-around;
}
</style>