<template>
  <div class="log">
   <div class="container">
    <div class="left">
      <n-card :bordered="false" class="login">
        <h1>Login</h1>
        <n-form
          ref="formRef"
          :rules="logrules"
          :model="logForm"
          label-placement="left"
          label-width="auto"
          require-mark-placement="right-hanging"
          :style="{
            maxWidth: '360px'
          }">
          <n-form-item label="账号" path="name">
            <n-input v-model:value="logForm.name" placeholder="请输入用户名"  />
          </n-form-item>
          <n-form-item label="密码" path="password">
            <n-input v-model:value="logForm.password" placeholder="请输入密码" />
          </n-form-item>
          <n-form-item>
            <n-grid :cols="12">
              <n-grid-item :span="4">
                <n-checkbox v-model:checked="logForm.remember" class="remem">
                  记住密码
                </n-checkbox>
              </n-grid-item>
              <n-grid-item :offset="5" :span="3">
                <n-button color="#252525" @click="SingIn" class="reginer">
                  去注册
                </n-button>
              </n-grid-item>
            </n-grid>
          </n-form-item>
          <n-form-item>
            <n-button color="#252525" @click="loginHandler">
              登录
            </n-button>
          </n-form-item>
        </n-form>
      </n-card>
      <n-card :bordered="false" class="register">
        <h1>Register</h1>
        <n-form
          :rules="regrules"
          label-placement="left"
          label-width="auto"
          require-mark-placement="right-hanging"
          :style="{
            maxWidth: '360px'
          }">
          <n-form-item label="账号" path="name">
            <n-input v-model:value="regForm.name" placeholder="请输入用户名"  />
          </n-form-item>
          <n-form-item label="密码" path="Spsd">
            <n-input v-model:value="regForm.Spsd" placeholder="请输入密码" />
          </n-form-item>
          <n-form-item label="手机" path="Spsd">
            <n-input v-model:value="regForm.phone" placeholder="请输入手机号" />
          </n-form-item>
          <n-form-item>
            <n-grid :cols="12">
              <n-grid-item :span="6">
                <n-form-item path="code">
                  <n-input v-model:value="regForm.code" placeholder="验证码" />
                </n-form-item>
              </n-grid-item>
              <n-grid-item :offset="2" :span="3">
                <n-form-item>
                  <n-button color="#252525" @click="SingIn" class="reginer">
                    发送验证码
                  </n-button>
                </n-form-item>
              </n-grid-item>
            </n-grid>
          </n-form-item>
          <n-form-item>
            <n-button color="#252525" @click="registerHandler" class="reginer">
              注册
            </n-button>
          </n-form-item>
        </n-form>
      </n-card>
    </div>
    <div class="right">
      <img src="../assets/img/Image.png" alt="">
    </div>
   </div>
    <div class="Cartoon">
      <div class="sun"></div>
      <div class="moon"></div>
      <div class="sea">
        <div class="inner-header"></div>
        <div>
          <svg class="waves" xmlns="http://www.w3.org/200/svg" xmlns:xLink="http://www.w3.org/1999/xlink"
            viewBox="0 24 150 28" preserveAspectRatio="none" shape-rendering="auto">
            <defs>
              <path id="gentle-wave"
              d="M-160 44c30 0 58-18 88-18s 58 18 88 18 58-18 88-18 58 18 88 18 v44h-352z"/>
            </defs>
            <g class="parallax">
              <use xlink:href="#gentle-wave" x="48" y="0" fill="rgba(255,255,255,0.7)"/>
              <use xlink:href="#gentle-wave" x="48" y="3" fill="rgba(255,255,255,0.5)"/>
              <use xlink:href="#gentle-wave" x="48" y="5" fill="rgba(255,255,255,0.3)"/>
              <use xlink:href="#gentle-wave" x="48" y="7" fill="#fff"/>
            </g>
          </svg>
        </div>
    </div>
    </div>
  </div>
</template>

<script setup>
import { ref,reactive,toRefs, onMounted } from "vue"
import { gsap } from "gsap"
import axios from "axios"

const state = reactive({
  logForm: ref({
    name: "",
    password: "",
    remember: false,
    phone: ""
  }),
  regForm: ref({
    name: "",
    Fpsd: "",
    phone: "",
    code: ""
  }),
  logrules: {
    name: {
      required: true,
      message: "请输入姓名",
      trigger: "blur"
    },
    password: {
      required: true,
      message: "请输入密码",
      trigger: ["password", "blur"]
    },
    remember: {
      required: false,
      message: "",
      trigger: "blur"
    }
  },
  regrules: {
    name: {
      required: true,
      message: "请输入姓名",
      trigger: "blur"
    },
    Fpsd: {
      required: true,
      message: "请输入密码",
      trigger: ["password", "blur"]
    },
    Spsd: {
      required: true,
      message: "请再次输入密码",
      trigger: ["password", "blur"]
    },
    phone: {
      required: true,
      message: "请输入手机",
      trigger: "blur"
    },
    code: {
      required: true,
      message: "请输入验证码",
      trigger: "blur"
    }
  },
})
     
const {logForm,regForm,logrules,regrules,} = {...toRefs(state)}

onMounted(() =>{
  const tl = gsap.timeline()
  tl.add(gsap.to(".sun",{
    opacity: 1,
    width: 240,
    height: 240,
    x: 800,
    y: -460,
    rotate: 400,
    duration: 2,
    delay: 1
  }))
  tl.to('.sun',{
    x: 805,
    y: -460,
    yoyo: true,
    repeat: -1,
    duration: 2
  }) 
})

const SingIn = () => {
  const tl = gsap.timeline()
  tl.add(gsap.to('.sun',{
    opacity: 0,
    width: 0,
    height: 0,
    x: 1600,
    y: -300,
    rotate: 400,
    duration: 2,
    delay: 1
  }))
  tl.to('.moon',{
    opacity: 1,
    width: 240,
    height: 240,
    x: 800,
    y: -460,
    rotate: 400,
    duration: 2,
    delay: 1
  })
  const tl2 = gsap.timeline()
  tl2.add(gsap.to('.login',{
    display: 'none',
    width: 0,
    height: 0,
    opacity: 0,
    y: -20, 
    duration: 2,
    delay: 1
  }))
  
  tl2.to('.register',{
    display: "block",
    y:-100,
    width: '100%',
    height: '100%',
    duration: 2,
  })
  gsap.to('.sea',{
    backgroundColor: '#252525',
    duration: 6
  })
}

const loginHandler = () => {
  axios({
    method:'post',
    url:'http://localhost:9090/userCheck',
    data: {
      username: logForm.value['name'],
      password: logForm.value['password']
    }
  }).then((response) => {
    console.log(response.data)
  })
}

const registerHandler = () => {
  axios({
    method:'post',
    url:'http://localhost',
    data: {
      username: regForm.value['name'],
      password: regForm.value['password'],
      phone: regForm.value['phone']
    }
  })
}

</script>

<style lang="less" scoped>
.log {
  display: flex;
  width: 100%;
  height: 100vh;
  justify-content: center;
  align-items: center;

  .container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    height: 64vh;
    opacity: 0.8;
    .left {
      border-radius: 10px 0 0 10px;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 60%;
      height: 100%;
      background-color: white;
      .n-card {
        h1{
          margin-bottom: 60px;
          text-align: center;
        }
        .n-form {
          .n-button{
            margin: auto;
          }
        }
      }
      .login {
        padding: 20px 60px;
        border-radius: 10px 0 0 10px;
        overflow: hidden;
      }
      .register {
        position: relative;
        top: 100px;
        width: 0;
        height: 0;
        display: none;
        padding: 20px 60px;
        border-radius: 10px 0 0 10px;
        overflow: hidden;
      }
    }
    .right {
      width: 40%;
      height: 100%;
      img {
        display: inline-block;
        width: 100%;
        height: 100%;
        border-radius: 0 10px 10px 0 ;
      }
    }
  }
}

.Cartoon {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100vh;
  left: 0;
  top: 0;
  .sun {
    position: absolute;
    left: -300px;
    bottom: 0;
    opacity: 0;
    width: 0;
    height: 0;
    background-color: #fbfd78;
    border-radius: 50%;
  }
  .moon {
    position: absolute;
    left: -300px;
    bottom: 0;
    opacity: 0;
    width: 0;
    height: 0;
    background-color: #fbfd78;
    border-radius: 50%;
  }
  .sea {
    position: relative;
    background-color: rgb(101, 101, 218);
    z-index: -2;
  }
  .inner-header {
    height: 70vh;
    width: 100%;
    margin: 0;
    padding: 0;
  }
  .waves {
    position: relative;
    width: 100%;
    height: 15vh;
    margin-bottom: -7px;
    min-height: 100px;
    max-height: 150px;
  }
  .parallax>use {
    animation: move-forever 25s cubic-bezier(.55,.5,.45,.5);
  }
  .parallax>use:nth-child(1) {
    animation-delay: -2s;
    animation-duration: 7s;
  }
  .parallax>use:nth-child(2) {
    animation-delay: -3s;
    animation-duration: 10s;
  }
  .parallax>use:nth-child(3) {
    animation-delay: -4s;
    animation-duration: 13s;
  }
  .parallax>use:nth-child(4) {
    animation-delay: -5s;
    animation-duration: 20s;
  }
  
  @keyframes move-forever {
    0% {
        transform: translate3d(-90px,0,0);
    }
    100% {
        transform: translate3d(85px,0,0);
    }
  }

  @media (max-width: 768px) {
    .waves {
        height: 40px;
        min-height: 40px;
    }
  }
}

</style>
