import { createRouter, createWebHashHistory } from 'vue-router'
import Main from '../views/Main'
import Login from '../views/Login'
import Home from '../components/Common/CommonHome'
import Personal from "../components/Common/CommonPersonal"
import Official from "../components/Common/CommonOfficial"
import Community from "../components/Common/CommonCommunity"
import Discuss from "../components/Common/CommonDiscuss"

const routes = [
  {
    path: '/',
    component: Main,
    redirect: '/home',
    children: [
      { path: '/home', name: 'home', component: Home },
      { path: '/Personal', name: 'Personal', component: Personal },
      { path: '/Official', name: 'Official', component: Official },
      { path: '/Community', name: 'Community', component: Community },
      { path: '/Discuss', name: 'Discuss', component: Discuss },
    ]
  },
  {
    path: '/login',
    name: 'login',
    component: Login
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
